input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 55px;
  border-radius: 10px;
  background-color: #fff;
  padding: 10px 25px !important;
  border: none!important;
  transition: all 0.4s;
}

.nexto-newsletter {
  max-width: 430px;
  position: relative;
  margin: 0 auto;
  input{
    padding: 10px 150px 10px 25px!important;
  }
  &.nexto-search{
    input{
      padding: 10px 150px 10px 50px!important;
    }
  }
}
button#nexto-search-btn {
  position: absolute;
  top: 16px;
  z-index: 9;
  left: 20px;
  opacity: .5;
}
#nexto-submit-btn{
  font-weight: 700;
  width: 130px;
  height: 45px;
  color: white;
  right: 5px;
  top: 5px;
  transition: all 0.4s;
  border-radius: 10px;
  position: absolute;
  background-size: 200% auto!important;
  background: linear-gradient(to right, #FF00D4 0%, #7D41EA 30.35%, #0080FF 100%);
  &:hover{
    background-position: right center;
  }
  @include max(575px) {
    position: inherit;
    width: 100%!important;
    margin-top: 10px;
    right: 0;
    top: 0;
}
}

.nexto-newsletter {
  &.nexto-newsletter2{
    max-width: 466px;
    margin: 45px 0 0;
    input{
      padding: 10px 160px 10px 25px!important;
    }
    @include max(991px) {
      max-width: 100%;
    }
    input{
      border-radius: 3px;
    }
    #nexto-submit-btn{
      border-radius: 3px;
      right: 0;
      top: 0;
      height: 55px;
      width: 150px;
      border-radius: 0px 3px 3px 0px;
      background: var(--gray-800);

    }
  }
}