.btn-video-light {
  height: 64px;
  width: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--gray-10);
  color: #fff;
  box-shadow: 0px 0px 0px 0px rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  padding: 0px !important;
  animation: rt-pulse 1.25s infinite;
  @include afterparent;
  transition: all 0.24s ease-in-out;
}
// animation
@keyframes rt-pulse {
  to {
    box-shadow: 0px 0px 0px 15px rgba(#fff, 0);
  }
}
.video-btn-center {
  .overlay-content {
    position: absolute;
    left: 0px;
    top: 0px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &.hp {
    .rt-btn {
      left: 40%;
    }
  }
}

.btn-light {
  background: var(--gray-10);
  border-color: var(--gray-10);
  color: var(--primary-500);
  &:hover,
  &:focus,
  &:active {
    background: darken(#fff, 4%);
    border-color: darken(#fff, 4%);
  }
  &.disabled,
  &:disabled {
    background: var(--primary-100);
    border-color: var(--primary-100);
  }
}

.remove-class {
  .btn {
    margin: 4px;
  }
  .icon-btn {
    display: inline-block;
    line-height: 44px;
  }
}
